<template >
    <div>
    <div id="container" :style="containerStyle" >
        <h2>carbon check</h2>
        <h1>{{msg}}</h1>
        <div :style="container2Style">
            <div :style="valueContainerStyle">
                <div :style="valueTitleStyle">Total</div>
                <div id="" :style="valueStyle"> 21.5</div>
                <div :style="unitStyle">kG of CO<sub>2</sub>e</div>
            </div>
            <div :style="valueContainerStyle">
                <div :style="valueTitleStyle">Average</div>
                <div :style="valueStyle">12.3</div>
                <div :style="unitStyle">kg of CO<sub>2</sub>e<br/>per kg food</div>
            </div>
        </div>
        <div id="chart-container">
            <canvas :style="chart" id="myChart"></canvas>
        </div>
        <v-switch color="primary" v-model="switch1" @change="changeSwitchState()" :label="switchUnit"></v-switch>
    </div>
        
</div>

</template>

<script>

import Chart from 'chart.js';


export default {
    name: 'Results',
    props: {
        msg: String
    },
    data()  {
        return {
            chartSize:0, 
            maxChartSize:600,
            chartMargin:14,
            windowWidth: 0,
            containerStyle:"padding:14px;"+  
                            "padding-top:28px;",
            container2Style: "margin-top:28px;"+
                                "height:100px;" +
                            "padding-bottom:45px;",

            valueContainerStyle: "width:50%;"+
                                "float:left;",
            valueTitleStyle: "fontSize:20px",
            valueStyle:     "fontSize:40px" ,
            unitStyle:      "fontSize:12px" ,
            chart:          "margin-top:54px;" +
                            "margin-bottom:30px;",
            accuracyStyle:  "fontSize:14px;" +
                            "margin-right:12px;" ,
            switch1: true,
            switchUnit: "Metric units",
            metricLabels:[
                    '50g Lettuce',
                    '15g Croutons',
                    '140g Chicken', 
                    '5g Anchovies'
                ],
            imperialLabels:[
                    '5oz Lettuce',
                    '1.5oz Croutons',
                    '14oz Chicken', 
                    '0.5oz Anchovies'
                ],
        }
    },
    created(){
        window.addEventListener('resize', this.handleResize);
        document.body.style.backgroundColor = "#fdf2d8";
    },
    mounted() {
        this.handleResize();
        this.createChart();
    },
    methods: {
        changeSwitchState(){
            if( this.switch1){
                this.switchUnit = "Metric units";
            }
            else{
                this.switchUnit = "Imperial units";
            }
            this.createChart();
        },
        createChart(){ 

            var ctx = document.getElementById('myChart');
            if(this.myDoughnutChart!=null){
                console.log('destroy');
                this.myDoughnutChart.destroy();
            }
            
            const context = ctx.getContext('2d');
            context.clearRect(0, 0, ctx.width, ctx.height);
  
            var labels1 =[];
            if( this.switch1 ==true){
                    labels1 = this.metricLabels;
                }
                else{
                    labels1 = this.imperialLabels;
                }
            var data = {

                datasets: [{
                      label: 'CO2e',
                    data: [10, 20, 30, 30],
                    backgroundColor: ['#fdd378', '#b9a2ff', '#ff93f9', "#7000bb"],
                }],

                // These labels appear in the legend and in the tooltips when hovering different arcs
                labels:labels1
            };
            // And for a doughnut chart
            this.myDoughnutChart = new Chart(ctx, {
                type: 'doughnut',
                data: data,
                options: {
                    legend:{
                        labels:{
                            padding:10,
                            fontSize:14,
                            fontColor:"#434343"
                        },
                        position:'left',
                        usePointStyle: true,
                        
                    },
                    layout: {
                        padding: {
                            left: 0,
                            right: 0,
                            top: 0,
                            bottom: 0
                        }
                    },
                    animation:{
                        animateScale: true 
                    },
                    cutoutPercentage: 50,
                    tooltips:{
                        xPadding: 10,
                        yPadding: 10,
                        //backgroundColor: 'rgba(54, 68, 77, 0.95)',
                        callbacks: {
                            title: function(tooltipItem, data){
                                return [ data.labels[tooltipItem[0].index] ];
                            },
                            label: function(tooltipItem, data) {      
                                return [ " " + data.datasets[0].data[tooltipItem.index] + " kg CO2e"];
                            }
                        }
                    }
                }
            });

            this.myDoughnutChart.canvas.parentNode.style.width = this.chartSize + 'px';

        },

        /**
        * handle resize
        */
        handleResize() {
            this.windowWidth = window.innerWidth;
            if(this.windowWidth > this.maxChartSize){
                this.chartSize = this.maxChartSize ;
                document.getElementById('chart-container').style.width = this.chartSize + 'px';
                document.getElementById('container').style.width = this.chartSize + 'px';
            }
            else{
                this.chartSize = window.innerWidth;
                document.getElementById('chart-container').style.width = this.chartSize +'px';
                document.getElementById('container').style.width = this.chartSize + 'px';
            }

            document.getElementById('container').style.marginLeft = ( this.windowWidth/2 - this.chartSize/2)+'px';
        },

        /**
        * handle resize
        */
        destroyed() {
            window.removeEventListener('resize', this.handleResize);
        },
        
     }

  };
</script>

